import React, { Component } from 'react';
import styled from 'styled-components';
import ProfilPictLarge from "../../Pictures/ProfilPictLarge.jpg"

class MySelf extends Component {
    render() {
        return (
            <Layout>
                <Block>
                    <Text>
                        <Title>ABOUT ME</Title>
                        <SubTitle>The last 5 years in early stage startups gave me a strong grasp on sales and digital marketing. During these experiences, I developed a passionate interest about the tech behind, and decided to learn to code </SubTitle>
                    </Text>
                    <ProfilPict src={ProfilPictLarge} alt="ProfilPict" />
                </Block>


            </Layout>
        );
    }
}

export default MySelf;

const Layout = styled.div`
padding-top: 5rem;
padding-bottom: 5rem;
width: 100vw;
height: 45vh;
background: #585d69;
display: flex;
justify-content: center;
`

const Block = styled.div`
display: flex;
justify-content: space-evenly;
flex-flow: row-reverse;
width: 80%;
flex-flow: row-reverse;
align-items: center;
    @media (max-width: 1050px){
        flex-flow: column-reverse;
    }

`

const ProfilPict = styled.img`    
border-radius: 50%;
display: inline-block;
height: 13rem;
vertical-align: middle;
width: 13rem;
align-self: center;
border: 5px solid;
border-color: white;
animation: bounce-left .6s;


@keyframes bounce-left {
    0% {
    opacity: 0;
    transform: translateX(-100px);
    }

    60% {
    opacity: 1;
    transform: translateX(20px);
    }
    100% {
    transform: translateX(0);
    }
}
    
`

const Text = styled.div`
font-size: 1em;
display: block;
width: 31rem;
@media (max-width: 700px) {
    width: 20rem;
}

animation: bounce-right .6s;

@keyframes bounce-right {
    0% {
    opacity: 0;
    transform: translateX(100px);
    }

    60% {
    opacity: 1;
    transform: translateX(-20px);
    }
    100% {
    transform: translateX(0);
    }
}
`

const Title = styled.div`
font-family : 'Roboto';
font-size: 5rem;
color: #D90770;
align-self: center;
z-index: 10;
    @media (max-width: 700px) {
        font-size: 3rem;
        text-align: center;
    }
`
const SubTitle = styled.div`
font-family : 'Roboto';
font-size: 1.1rem;
color: white;
align-self: center;
z-index: 10;
    @media (max-width: 700px) {
        font-size: 1rem;
    }
`



